import React from 'react';
import { SendEmailPrivate } from '../components/helpers/Emailer';

const packages = [
    {
        title: '1 Hour',
        value: '50.00'
    },

    {
        title: '2 Hours',
        value: '100.00'
    },

    {
        title: '3 Hours',
        value: '150.00'
    },

    {
        title: '4 Hours',
        value: '200.00'
    },

    {
        title: '5 Hours',
        value: '250.00'
    },

    {
        title: '6 Hours',
        value: '270.00'
    },
];

export default class RegisterPrivate extends React.Component {
    constructor(props) {
        super(props)
        var initialForm = {
            'selectedPackage': packages[0].title,
            'selectedPrice': packages[0].value
        }

        this.state = ({ packageList: packages, currentForm: initialForm, permitFile: '' });
        this.dropdownChanged = this.dropdownChanged.bind(this)
        this.formSubmitted = this.formSubmitted.bind(this)
        this.redStar = this.redStar.bind(this)
        this.handleFormChange = this.handleFormChange.bind(this)
        this.handleCheckboxChange = this.handleCheckboxChange.bind(this)
        this.updateNumberFormLogic = this.updateNumberFormLogic.bind(this)
        this.autonumIncrement = this.autonumIncrement.bind(this)
    }

    handleCheckboxChange(event) {
        var currentFormCopy = this.state.currentForm
        currentFormCopy[event.target.name] = event.target.checked
        this.setState({ currentForm: currentFormCopy })
    }

    updateNumberFormLogic(event, startField, endField, digit) {
        if (event.target.name === startField && event.target.value.length > digit - 1) {
            var textVal = event.target.value.substring(0, digit)
            var currentFormCopy = this.state.currentForm
            currentFormCopy[startField] = textVal
            this.setState({ currentForm: currentFormCopy })
            document.getElementById(endField).focus()
        }
    }

    autonumIncrement(event) {
        //now for changing cursor for phone number boxes
        this.updateNumberFormLogic(event, 'home_phone1', 'home_phone2', 3)
        this.updateNumberFormLogic(event, 'home_phone2', 'home_phone3', 3)
        this.updateNumberFormLogic(event, 'home_phone3', 'cell_phone1', 4)
        this.updateNumberFormLogic(event, 'cell_phone1', 'cell_phone2', 3)
        this.updateNumberFormLogic(event, 'cell_phone2', 'cell_phone3', 3)
        this.updateNumberFormLogic(event, 'cell_phone3', 'best_time_to_call', 4)
    }

    handleFormChange(event) {
        var currentFormCopy = this.state.currentForm
        currentFormCopy[event.target.name] = event.target.value
        this.setState({ currentForm: currentFormCopy })
        this.autonumIncrement(event)
    }

    dropdownChanged(event) {
        var val = this.state.packageList.find(o => o.title === event.target.value).value
        var currentFormCopy = this.state.currentForm
        currentFormCopy['selectedPackage'] = event.target.value
        currentFormCopy['selectedPrice'] = val
        this.setState({ currentForm: currentFormCopy })
    }

    formSubmitted(event) {
        console.log(this.state.currentForm)
        var sentEmail = SendEmailPrivate(this.state.currentForm)
        if(sentEmail === true){
            alert('Data saved! Please give us a call if you do not hear back within 24 hours')
            document.getElementById('formSubmit').disabled = true
        } 
        else 
        {
            alert('Error submitting form! Please try again! If error persists, please give us a call!')
        }
        event.preventDefault()
    }

    redStar() {
        return (<span style={{ color: "#AB1A1A", fontWeight: "bolder" }}>*</span>)
    }

    render() {
        return (
            <div className="container">
                <h2> Register for Private Lessons </h2><br />
                <form onSubmit={this.formSubmitted}>
                    <div className="container">
                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="student_name">Student Name: {this.redStar()}</label>
                            </div>
                            <div className="col input-group-append">
                                <input type="text" name="student_first_name" size="12" value={this.state.currentForm.student_first_name} onChange={this.handleFormChange} required className="form-control" id="student_first_name" placeholder="First Name" />
                                <input type="text" name="student_last_name" size="12" value={this.state.currentForm.student_last_name} onChange={this.handleFormChange} required className="form-control" id="student_last_name" placeholder="Last Name" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="street_address">Street Address: {this.redStar()}</label>
                            </div>
                            <div className="col">
                                <input type="text" name="street_address" value={this.state.currentForm.street_address} onChange={this.handleFormChange} required className="form-control" id="street_address" placeholder="Street Address" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="city">City: {this.redStar()}</label>
                            </div>
                            <div className="col">
                                <input type="text" name="city" value={this.state.currentForm.city} onChange={this.handleFormChange} required className="form-control" id="city" placeholder="City" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="zip_code">Zip Code: {this.redStar()}</label>
                            </div>
                            <div className="col">
                                <input type="text" name="zip_code" value={this.state.currentForm.zip_code} onChange={this.handleFormChange} required className="form-control" id="zip_code" placeholder="Zip Code" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="student_email">Student Email: {this.redStar()}</label>
                            </div>
                            <div className="col">
                                <input type="email" name="student_email" value={this.state.currentForm.student_email} onChange={this.handleFormChange} required className="form-control" id="student_email" placeholder="Student Email" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="home_phone1">Home Phone:</label>
                            </div>
                            <div className="col input-group-append">
                                <input type="text" name="home_phone1" size="2" value={this.state.currentForm.home_phone1} onChange={this.handleFormChange} className="form-control" id="home_phone1" placeholder="(000)" />
                                <input type="text" name="home_phone2" size="2" value={this.state.currentForm.home_phone2} onChange={this.handleFormChange} className="form-control" id="home_phone2" placeholder="000" />
                                <input type="text" name="home_phone3" size="2" value={this.state.currentForm.home_phone3} onChange={this.handleFormChange} className="form-control" id="home_phone3" placeholder="000" />
                            </div>
                        </div>
                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="cell_phone">Cell Phone: {this.redStar()}</label>
                            </div>
                            <div className="col input-group-append">
                                <input type="text" name="cell_phone1" size="2" value={this.state.currentForm.cell_phone1} onChange={this.handleFormChange} className="form-control" id="cell_phone1" placeholder="(000)" />
                                <input type="text" name="cell_phone2" size="2" value={this.state.currentForm.cell_phone2} onChange={this.handleFormChange} className="form-control" id="cell_phone2" placeholder="000" />
                                <input type="text" name="cell_phone3" size="2" value={this.state.currentForm.cell_phone3} onChange={this.handleFormChange} className="form-control" id="cell_phone3" placeholder="000" />
                            </div>
                        </div>

                        <div className="w-100"></div>
                        <div className="row">&nbsp;</div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="best_time_to_call">Best Time to Call:</label>
                            </div>
                            <div className="col">
                                <input type="text" name="best_time_to_call" value={this.state.currentForm.best_time_to_call} onChange={this.handleFormChange} className="form-control" id="best_time_to_call" placeholder="3 hour window when we can call" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="learners_permit_txt">Learner's Permit:</label>
                            </div>
                            <div className="col input-group">
                                <input type="text" name="learners_permit_txt" value={this.state.currentForm.learners_permit} onChange={this.handleFormChange} className="form-control" id="learners_permit_txt" placeholder="Learner's Permit ID" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="dob">Date of Birth: {this.redStar()}</label>
                            </div>
                            <div className="col">
                                <input type="date" name="dob" value={this.state.currentForm.dob} onChange={this.handleFormChange} required className="form-control" id="dob" placeholder="Date of Birth" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="starting_date">Starting Date:</label>
                            </div>
                            <div className="col">
                                <input type="date" name="starting_date" value={this.state.currentForm.starting_date} onChange={this.handleFormChange} className="form-control" id="starting_date" placeholder="Starting Date" />
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="comments">Comments:</label>
                            </div>
                            <div className="col">
                                <textarea type="text" name="comments" value={this.state.currentForm.comments} onChange={this.handleFormChange} className="form-control" id="comments" placeholder="Comments (Optional)" />
                            </div>
                        </div>

                        <div className="row">
                            <div className="col text-center">
                                <label htmlFor="package">Select a Private Package:</label>
                            </div>
                            <div className="col input-group">
                                <select name="package" onChange={this.dropdownChanged}>
                                    {Object.keys(this.state.packageList).map((key, index) => (
                                        <option value={this.state.packageList[index].title} key={this.state.packageList[index].title}>{this.state.packageList[index].title}</option>
                                    ))};
                                </select>
                                <div className="input-group-append">
                                    <span className="input-group-text">$ {this.state.currentForm.selectedPrice}</span>
                                </div>
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                &nbsp;
                            </div>
                            <div className="col text-left">
                                <br />
                                <input type="checkbox" checked={!!this.state.currentForm.price_agree} onChange={this.handleCheckboxChange} required name="price_agree" id="price_agree">
                                </input>
                                &nbsp;{this.redStar()}I have reviewed and agree upon the price shown above! <br />
                                (Credit / Debit card payments done via PayPal will be subject to 3% service charge)
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col text-center">
                                &nbsp;
                            </div>
                            <div className="col text-left">
                                <br />
                                <input type="checkbox" checked={!!this.state.currentForm.terms_agree} onChange={this.handleCheckboxChange} required name="terms_agree" id="terms_agree">
                                </input>
                                &nbsp;{this.redStar()}I have read and agree with the Terms and Conditions.
                            </div>
                        </div>

                        <div className="w-100"></div>

                        <div className="row">
                            <div className="col">
                                &nbsp;
                            </div>
                            <div className="col text-left">
                                <br />
                                <input className="btn btn-primary" id="formSubmit" type="submit" value="Submit" />
                            </div>
                        </div>
                    </div>

                </form>
            </div >
        );
    }
}